* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;


  }
  html, body {
  overflow-x: hidden;
}
  
  
  .navbar {
    height: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 4rem;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1000;
    background-color: #e6e7e8;
    transition: background-color 0.5s ease-in-out, transform 1s ease-in-out;
}

.navbar.scrolled {
    background-color: white;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    transform: translateY(-10px);
}

  
  
  .logo {
  font-size: 1.6rem;
  font-weight: bold;
  color: #111827;
  background-color: transparent;
  border: none;
  margin-left: 0;
}

 /* .nav-links {
  display: flex;
  gap: 1.5rem;
} */
 /* smooth animation */
 .nav-links {
  display: flex;
  gap: 1.5rem;
  transition: all 0.3s ease-in-out;
}
  .nav-links a {
    text-decoration: none;
    color: #111827;
    font-weight: 500;
    transition: color 0.3s;
  }
  
  .nav-links a:hover {
    color: #6366f1;
  }
  
 .hamburger {
  display: none;
  flex-direction: column;
  cursor: pointer;
  
}

.hamburger div {
  width: 25px;
  height: 3px;
  background-color: #111;
  margin: 4px 0;
}
