 /* MOBILE */
@media (max-width: 768px) {
.navbar {
    padding: 0 10px;
  }

  .nav-links {
    display: flex;
    position: absolute;
    top: 80px;
    left: 0;
    width: 100%;
    flex-direction: column;
    background-color:  #e6e7e8;
    padding: 1rem 0;
    gap: 1rem;
    text-align: center;
    z-index: 999;

    /* 👇 Animation properties */
    max-height: 0;
    opacity: 0;
    overflow: hidden;
    transform: translateY(-10px);
    transition: all 0.4s ease;
  }

  /* 👇 When menu is open */
  .nav-links.show {
    max-height: 400px; /* enough space for all items */
    opacity: 1;
    transform: translateY(0);
  }
 .navbar.scrolled .nav-links{
    background-color: white; /* same color you use normally */
  }
  .hamburger {
    display: flex;
    flex-direction: column;
    cursor: pointer;
  }

  .hamburger div {
    width: 25px;
    height: 3px;
    background-color: #111;
    margin: 4px 0;
    transition: all 0.3s ease;
  }

  /* Optional: Add a simple X animation when open */
  .nav-links.show ~ .hamburger div:nth-child(1) {
    transform: rotate(45deg) translate(5px, 5px);
  }

  .nav-links.show ~ .hamburger div:nth-child(2) {
    opacity: 0;
  }

  .nav-links.show ~ .hamburger div:nth-child(3) {
    transform: rotate(-45deg) translate(6px, -6px);
  }

